import React, { Component } from 'react';
import './App.css';
import {DragDropContext, Droppable} from 'react-beautiful-dnd'
import initialData from './initial-data';
import Column from './components/Column'

class App extends Component{
  state = initialData;

  addTask = (current_column) => {
    let newState = this.state;
    const number = Object.keys(newState.tasks).length + 1;
    const add_task = 'task-' + number;
    newState.tasks[add_task] = {id: add_task, content:'New task'};
    newState.columns[current_column]["taskIds"].push(add_task)

    this.setState(newState);
  }

  deleteTask = (task,column_id) => {
      let newState = this.state;
      const index = newState.columns[column_id].taskIds.indexOf(task.id);
      newState.columns[column_id].taskIds.splice(index,1);
      delete newState.tasks[task.id];
      this.setState(newState);
  }

  addColumn = () => {
    let newState = this.state;
    const number = Object.keys(newState.columns).length + 1
    const add_column = "column-"+number
    newState.columnOrder.push(add_column)
    newState.columns[add_column] = {id:add_column, title: "New column", taskIds: []};
    this.setState(newState);
  }

  deleteColumn = (column_id) => {
      let newState = this.state;
      const index = newState.columnOrder.indexOf(column_id);
      newState.columnOrder.splice(index, 1);
      delete newState.columns[column_id];
      this.setState(newState);
  }

  editColumn = (column_id, e) => {
      let newState = this.state;
      newState.columns[column_id].title = e.target.value;
      this.setState(newState);
  }

  editTask = (task_id, e) => {
      let newState = this.state;
      newState.tasks[task_id].content = e.target.value;
      this.setState(newState);
  }

  onDragEnd = result => {
    const {destination, source, draggableId, type} = result;

    if(type === 'column'){
      const newColumnOrder = Array.from(this.state.columnOrder);
      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggableId);

      const newState = {
        ...this.state,
        columnOrder: newColumnOrder,
      };
      this.setState(newState);
      return;
    }
    if(!destination){
      return
    }

    if(
      destination.draggableId === source.droppableId &&
      destination.index === source.index
    ){
      return;
    }

    const start = this.state.columns[source.droppableId];
    const finish = this.state.columns[destination.droppableId];

    // Within column
    if(start === finish){
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index,0,draggableId);

      const newColumn = {
        ...start,
        taskIds: newTaskIds,
      };

      const newState = {
        ...this.state,
        columns: {
          ...this.state.columns,
          [newColumn.id]: newColumn
        }
      };

      this.setState(newState);
      return;
    }

    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index,1);
    const newStart = {
      ...start,
      taskIds: startTaskIds,
    };

    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finish,
      taskIds: finishTaskIds,
    };

    const newState = {
      ...this.state,
      columns: {
        ...this.state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish,
      }
    };

    this.setState(newState);

  };

  render(){
    return(
      <div>
        <button onClick={() => this.addColumn()} className="add_column">Add column</button>
        <DragDropContext onDragEnd={this.onDragEnd}>
          <Droppable droppableId="all-columns" direction="horizontal" type="column">
            {provided => (
            <div className="Board"
            {...provided.droppableProps}
            ref={provided.innerRef}
            >
              {this.state.columnOrder.map((columnId, index) => {
              const column = this.state.columns[columnId];
              const tasks = column.taskIds.map(taskID => this.state.tasks[taskID]);

              return <Column editTask={this.editTask} editColumn={this.editColumn} deleteColumn={this.deleteColumn} deleteTask={this.deleteTask} addTask={this.addTask} key = {column.id} column={column} tasks={tasks} index={index}/>;
              })}
            </div>
            )}
          </Droppable>
        </DragDropContext>
      </div>



  );}
}

export default App;
