import React, { Component } from 'react';
import Task from './Task';
import {Draggable,Droppable} from 'react-beautiful-dnd';
import './Column.css';
import Button from './Button'
import logo from './drag-icon.png';


class Column extends Component {
    render(){
        return(
        <Draggable draggableId={this.props.column.id} index={this.props.index}>
            {provided => (
                <div className="Column"
                {...provided.draggableProps}
                ref={provided.innerRef}
                >
                    <div className="column-title">
                        <img src={logo}
                        {...provided.dragHandleProps}
                        />
                        <input className="edit-title" type="text" defaultValue={this.props.column.title} onChange={(event)=>this.props.editColumn(this.props.column.id, event)}/>
                        <button onClick={() => this.props.deleteColumn(this.props.column.id)}className="delete">x</button>
                    </div>

                    <Droppable droppableId={this.props.column.id} type="task" className="Drop">
                        {(provided, snapshot) => (
                        <div
                        className="column-contents"
                        ref={provided.innerRef}
                        {...provided.droppableProps}
                        >
                            {this.props.tasks.map((task, index) =>
                                (<Task editTask={this.props.editTask} column_id={this.props.column.id} deleteTask={this.props.deleteTask} key={task.id} task={task} index={index} />
                            ))}

                        {provided.placeholder}
                        </div>
                    )}
                    </Droppable>
                    <Button addTask={this.props.addTask} current={this.props.column.id}/>
                </div>
            )}
        </Draggable>
        )
    }
}

export default Column;
