import React, { Component } from 'react';
import './Task.css';
import {Draggable} from 'react-beautiful-dnd';
import logo from './drag-icon.png';

class Task extends Component{
    render(){
        const isDragDisabled = false
        return(
        <Draggable isDragDisabled={isDragDisabled} draggableId = {this.props.task.id} index={this.props.index} disabled="false">
            {(provided, snapshot) => (
                <div
                {...provided.draggableProps}

                ref={provided.innerRef}
                isDragDisabled={isDragDisabled}
                className="Task"
                >
                    <img src={logo}
                    {...provided.dragHandleProps}
                    />
                    <input className="task-edit" type="text" defaultValue={this.props.task.content} onChange={(event)=>this.props.editTask(this.props.task.id, event)}/>
                    <button onClick={() => this.props.deleteTask(this.props.task,this.props.column_id)} className="delete">x</button>
                </div>
            )}

        </Draggable>
        );
    }
}

export default Task;
